.about {
	width: 100%;
	min-height: 635px;
	background-image: url('../images/decor/bg/bg6.jpg');
	background-repeat: no-repeat;
	background-size: 100% 100%;
	background-position: center center;
	position: relative;
	padding: rem(20) 0 rem(170) 0;
	overflow: hidden;
	@include tablet {
		padding: rem(20) 0 rem(132) 0;
	}
}

.zabor-bg {
	position: absolute;
	background-image: url('../images/decor/bg/footer_zabor.png');
	background-repeat: no-repeat;
	background-size: 100% 100%;
	background-position: bottom center;
	bottom: 0;
	left: 0;
	right: 0;
	//height: 115px;
	height: 125px;
	z-index: 0;
	transform: translateY(7px);
	@include tablet {
		height: 87px;
	}
}

.about__card {
	width: 100%;
	height: 100%;
	display: flex;
    justify-content: space-between;
    overflow: hidden;
    @include tablet {
    	flex-direction: column;
    	align-items: center;

    }
}

.about__img-wr {
	@include size(rem(361));
	border-radius: 50%;
	flex-shrink: 0;
	background-size: cover;
	background-position: center center;
	@include laptop {
		@include size(20rem);
	}
	@include tablet {
		margin-bottom: rem(30);
		@include size(20rem);
	}
}

.about__descrip {
	font-size: rem(22);
	font-weight: 300;
	font-style: normal;
	line-height: 1.425;
	letter-spacing: 1.3px;
	text-align: left;
	color: $primaryColor;
	width: 65%;
	@include laptop {
		width: 70%;
	}
	@include custom {
		width: 798px;
	}
	@include tablet {
		width: 100%;
	}
}

.about__text {
	margin-bottom: rem(30);
	@include tablet {
		text-align: center;
	}
}

.about__author {
	margin-bottom: rem(25);
	@include tablet {
		text-align: right;
	}
}

.about__contacts {
	width: 100%;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	@include tablet {
		justify-content: center;
	}
}

.about__whatsApp, .about__phone {
	max-width: rem(375);
	width: 45%;
	height: rem(55);
	border-radius: rem(27,5);
	margin-right: rem(20);
	overflow: hidden;
}

.about-phone__link {
	display: flex;
	width: 100%;
	height: 100%;
	color: $text-second-color;
	font-size: rem(20);
	align-items: center;
	font-weight: bold;
	font-style: normal;
	justify-content: center;
	transition: color 0.2s, background-color 0.2s;
	letter-spacing: 1.4px;
	padding: rem(2) rem(11);
	background-color: $primaryColor;
	&:hover, &:active {
		color: rgba(#77b3d4, 0.95);
		background-color: rgba($primaryColor, 0.99);
	}
	&:hover svg, &:active svg{
		fill: currentColor;
	}
	@include custom {
		font-size: rem(24);
	}
	& span {
		margin: 0 auto;
	}
}

.about-whatsApp__link {
	display: flex;
	width: 100%;
	height: 100%;
	color: $text-second-color;
	font-size: rem(20);
	align-items: center;
	font-weight: bold;
	font-style: normal;
	transition: color 0.2s, background-color 0.2s;
	letter-spacing: 1.4px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	padding: rem(2) rem(11);
	background-color: $primaryColor;
	&:hover, &:active {
		color: rgba(#57bb63, 0.95);
		background-color: rgba($primaryColor, 0.99);
	}
	&:hover svg, &:active svg{
		fill: currentColor;
	}
	@include custom {
		font-size: rem(24);
	}
	& span {
		margin: 0 auto;
	}
}

.call-us-svg {
	@include size(rem(40));
	margin-right: rem(10);
	fill: #77b3d4;
	stroke: $primaryColor;
	transition: fill 0.2s;
}

.whatsapp-svg {
	@include size(rem(40));
	margin-right: rem(10);
	fill: #57bb63;
	stroke: $primaryColor;
	transition: fill 0.2s;
}


