.calculation {
	width: 100%;
	height: 88.52vh;
	min-height: 720px;
	display: flex;
	flex-direction: column;
	overflow: hidden;
	position: relative;
	background-color: $bgColor;
	padding-bottom: rem(150);
	@include custom {
		padding-bottom: 1rem;
	}
	@include desktop {
		padding-bottom: 0;
	}
	@include tablet {
		height: initial;
		padding-bottom: rem(10);
	}
}

.calculation__title {
	width: 100%;
	min-height: rem(98);
	background-color: #f2a000;
	padding: rem(30) 0;
	@include laptop {
		padding: 0.6rem 0;
	}
	@include tablet {
		padding: 0.6rem 0;
	}
}

.calculation__wrapper {
	width: 100%;
	min-height: 615px;
	background-repeat: no-repeat;
	background-position: bottom center;
	background-size: cover;
	position: relative;
	@include tablet {
		min-height: 875px;
		background-position: center center;
		background-size: 100% 100%;
	}
}

.rulon-decor {
	@include size(100%, 100%);
	position: absolute;
	z-index: 0;
	top: 0;
	left: 0;
	background-position: center bottom;
	background-size: 100% 100%;
}

.rulon-decor__img {
	max-width: 100%;
	height: auto;
}

.calculation__left {
	width: 49.4%;
	height: 100%;
	@include tablet {
		width: 90%;
		padding: rem(10) 0;
	}
}
.promo-card {
	width: 100%;
	height: 100%;
	@include tablet {
		margin-bottom: 1.7rem;
	}
}

.calculation__right {
	width: 45%;
	max-width: rem(520);
	height: 100%;
	@include tablet {
		width: 90%;
		margin-bottom: rem(60);
	}
}

#order-calculation {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: flex-end;
}

.promo-card__content {
	display: flex;
	flex-direction: column;
}

.promo-card__description {
	font-size: rem(18);
	font-weight: bold;
	font-style: normal;
	line-height: 1.425;
	letter-spacing: 1.1px;
	text-align: left;
	color: $primaryColor;
}

.promo-card__item {
	display: flex;
	align-items: center;
	margin-bottom: rem(10);
	&:last-child {
		margin-bottom: 0;
	}
}

.check-svg {
	@include size(rem(40));
	margin-right: rem(10);
	flex-shrink: 0;
}

.promo-card__price {
	color: $primaryColor;
	font-weight: bold;
	font-style: normal;
	text-align: left;
	width: 100%;
}

.promo-card__price-title {
	font-size: rem(30);
	line-height: 2;
	letter-spacing: 1.8px;
	@include tablet {
		font-size: rem(25);
	}
}

.promo-card__price-amount {
	font-size: rem(60);
  	line-height: 1;
  	letter-spacing: 3.6px;
  	@include tablet {
  		font-size: rem(50);
  		line-height: 1.25;
  	}
}


