.title {
	color: $primaryColor;
	font-size: rem(36);
	font-weight: bold;
	font-family: 'open_sans';
	font-style: normal;
	letter-spacing: 2.2px;
	text-align: center;
	line-height: 1.2;
	width: 100%;
	vertical-align: middle;
	@include tablet {
		//width: 80%;
		font-size: rem(30);
		line-height: 2.6;
	}
	@include s-mobile {
		//width: 90%;
		font-size: rem(19);
	}
	&.text-color {
		color: $text-second-color;
		margin-bottom: rem(70);
		@include tablet {
			margin-bottom: rem(30);
			line-height: 1.34;
		}
	}
	&.lh-1-34 {
		@include tablet {
			line-height: 1.34;
		}
	}
	&.bg-orange {
		width: 60%;
		line-height: 1.5;
		@include desktop {
			width: 76%;
		}
		@include laptop {
			width: 100%;
		}
		@include tablet {
			width: 80%;
			font-size: rem(30);
		}
		@include s-mobile {
			width: 90%;
			font-size: rem(19);
		}
	}
	&.bg-orange span {
		background-color: $orange-color;
	}
	&.mb-25 {
		margin-bottom: rem(20);
	}
	&.mb-80 {
		margin-bottom: rem(80);
		@include tablet {
			margin-bottom: rem(30);
		}
	}
}

.card-title {
	font-size: rem(30);
	font-weight: bold;
	line-height: 1.2;
	letter-spacing: 1.8px;
	text-align: left;
	color: $primaryColor;
	margin-bottom: rem(15);
	@include tablet {
		margin: 0 -5px 15px -5px;
		text-align: center;
	}
	&.text-color {
		text-align: center;
		color: $text-second-color;
		width: 100%;
		margin-bottom: rem(20);
	}		
}


